body {
  background: linear-gradient(238deg, #fd8800, #fd008f, #9700fd, #003dfd, #05c7e6, #4bd58d);
  background-size: 1200% 1200%;
  -webkit-animation: rainbow 30s ease infinite;
  animation: rainbow 30s ease infinite;
}

@-webkit-keyframes rainbow {
  0% { background-position: 0% 50% }
  50% { background-position: 100% 50% }
  100% { background-position: 0% 50% }
}
@keyframes rainbow {
  0% { background-position: 0% 50% }
  50% { background-position: 100% 50% }
  100% { background-position: 0% 50% }
}

// Controller.js

.controller {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  margin-top: 15px;

  button {
    font-size: 1.1rem;
    font-weight: 700;
    border: 1px solid #fff;
    background-color: transparent;
    color: #fff;
    border-radius: 6px;
    outline: none;
    width: 85px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    margin: 15px;
    text-transform: uppercase;
    opacity: 0.7;
  }

  button:hover {
    cursor: pointer;
    background-color: #000;
    opacity: 0.7;
  }
}

// Settings.js

.settings {
  display: flex;
  display: -webkit-flex;
  justify-content: space-around;
  margin-top: 50px;
}

.settings-section {
  text-align: center;
  height: 100px;
  opacity: 0.7;

  label {
    font-weight: 700;
    line-height: 3;
    opacity: 0.7;
  }

  button {
    font-size: 1.1rem;
    font-weight: 700;
    border: 1px solid #fff;
    background-color: transparent;
    color: #fff;
    border-radius: 6px;
    outline: none;
    width: 35px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    opacity: 0.7;
  }

  button:not(.disable):hover {
    cursor: pointer;
    background-color: #000;
  }

  button.disable {
    opacity: 0.5;
    cursor: not-allowed;
  }

  span {
    width: 45px;
    height: 45px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    color: #444;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 1.1rem;
  }
}

// Times.js

.times {
  width: 100%;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
}

.times-content {
  display: inline-block;
  margin: 0 auto;
  width: 280px;
  height: 280px;
  margin-top: 25px;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 5px solid #fff;
  border-radius: 50%;
  opacity: 0.7;
}

#timer-label {
  font-size: 1.5rem;
  font-weight: 700;
}

#time-left {
  font-size: 5rem;
  font-weight: 700;
}

// Extra

#unicorn {
  width: 270px;
  height: auto;
  position: fixed;
  left: 80px;
  bottom: 0;
}
